body{
    background: #F7F9F9;   
    height:100%;
}

.left-div{
    background:#F0F8F9    ;
    text-align: center
}
.left-div textarea{
    display: block;
    width:80%;
    height:300px;
    padding:0;
    
}
.left-div div{
    display:block !important;
    width: 100%;
}
.right-div{
    height:100%;
    width:100%;
    background:#fff;
    display: flex;
  align-items: center;
  justify-content: center;
    
}
.main-wrapper{
    display: flex;
    flex-direction: column;
       position: relative;
   
}
.main-wrapper textarea:focus, .main-wrapper textarea:active{
    outline: none;
}
.main-wrapper textarea{
    border:none;
   padding-top:20px;
    width:100%;
    height:100%;
    overflow: hidden;
}
.transform-wrapper{
   width:80%;
   margin:0 auto;
    min-height: 17em;
}
.type-wrapper{
    padding: 0 15px;
    
}
.type-wrapper div div{
    min-height:16em;
}
.type-wrapper div div:last-child{
padding-top:20px;
}
.type-wrapper div div:first-child{
border-right:1px solid #ddd;
}
.how-to{
    width:80%;
   margin:50px auto;
}
.how-to div:first-child span{
    color:gray;
    text-align: justify;
}
.how-to div:last-child table{
    background: #FBFFFF
}
.how-to div:last-child table{
    margin-top:15px;
}
header.logo{

padding: 15px 100px;
border-bottom: 1px solid #ddd;
background: #fff;
   margin-bottom: 50px;
}
.footer{
        width:100%;
        background: #eee;
       padding:20px;
      
      
}
.transformed_text{
    white-space: pre-line;
}
.copy_to_clip:hover{
    background-color: blue;
}
.copy_to_clip{
    background-color: rgba(0, 0, 0, 0.15);
    position: absolute;
    bottom:5px;
    right:10px;
    cursor: pointer;
}